import uniPopup from "@/components/uni-popup/uni-popup.vue"
export default {
	components:{
		uniPopup
	},
	data() {
		return {
			title: 'Hello',
			clearBtnShow:false,
			search_time_dc:'',// 搜索时间_单程
			search_time_fc:'',// 搜索时间_返程
			startCityInfo: {
				AirportShortName: '出发机场',
			},
			endCityInfo: {
				AirportShortName: '到达机场'
			},
			// 选中时间距当前时间的差距日
			time_to_now:'',
			gotime: [{
					time: '12/7',
					day: '周一'
				},
				{
					time: '12/7',
					day: '周一'
				},
			],
			qcpHist:[],// 汽车票历史记录
			sit_type:0,// 舱位编号 0 全部 1 经济 2 公务/头等
			IsChild:false,// 是否为儿童票 需要单独设置并订票时传导
			ticTypeList:[
				{
					title:'单程',
					type:0,
				},
				{
					title:'往返',
					type:1,
				},
				{
					title:'联程',
					type:2,
				},
			],// 机票类型数组,
			ticType:0,// 机票类型 单程 = 0, 往返 = 1, 联程 =2
			ticTypeStr:'往返',
			cfDate: '',
			cfDateStr: '选择日期',
			// fcDate: '',  // 返程日期
			// fcDateStr: '添加返程',
			isReading: true, // 防抖
			
		}
	},
	onShow(){
		this.init()
	},
	onLoad(opt) {
	},
	onUnload(){
		uni.hideLoading()
	},
	methods: {
		// 初始化
		init(){
			this.isReading = true
			
			let _this = this;
			// 初始化日期
			if(Object.keys(this.$store.state.airFlightType.airDate).length == 0){
				this.initDate() // 自动初始化日期
			}else{
				// this.cfDatefun(this.$store.state.airFlightType.airDate) // 选择日期
				let datas = this.$store.state.airFlightType.airDate
				let reg = /^[0]+/
				console.log('飞机时间', datas)
				let month = datas.date.month.toString().replace(reg, '')
				let day = datas.date.day.toString().replace(reg, '')
				let recent = datas.recent ? ` (${datas.recent})` : ''
				this.cfDateStr = month + '月' + day + '日' + recent
				this.cfDate = datas.dateStr
			}
			
			uni.getStorage({
				key: 'FromAirPlane',
				success(res) {
					console.log('From res', res.data)
					_this.startCityInfo = res.data
				}
			})
			
			uni.getStorage({
				key: 'ToAirPlane',
				success(res) {
					console.log('To res', res.data)
					_this.endCityInfo = res.data
				}
			})
			
			
		},
		// fcDatefun(target){
		// 	if(Object.keys(target).length == 0){
		// 		return 
		// 	}else{
		// 		this.fcDateStr = target.date.month + '月' + target.date.day + '日'
				
		// 	}
		// },
		// 进入页面自动初始化当前日期
		initDate(){
			let year = new Date().getFullYear()
			let month = new Date().getMonth() + 1
			let day = new Date().getDate()
			this.cfDate = year + '/' + month + '/' + day  //cfDate
			this.cfDateStr = month + '月' + day + '日'
		},
		cfDatefun(target){
			if(Object.keys(target).length == 0){
				return 
			}else{
				this.cfDateStr = target.date.month + '月' + target.date.day + '日'
				
			}
		},
		// 删除往返时间，变回单程机票
		delEndTime(){
			let obj = this.$store.state.airFlightType
			obj.end_time = '',
			this.ticType = 0
			this.ticTypeStr = '单程'
		},
		
		// 设置时间格式(月份个位数带0)
		setTimeMonth(Time){
			let year = new Date(Time).getFullYear() // 获取到年
			let month = new Date(Time).getMonth() + 1 // 获取到月
			let day = new Date(Time).getDate() // 获取到日期
			let str = ''
			if (month<10) {
				month = '0'+month
			}
			str = year + '/' + month + '/' + day
			console.log(str)
			return str
		},
		
		// 自动设置机票单程/往返时间
		setTicTypeTime(){
			let airTimeDes = this.$store.state.airFlightType
			if (airTimeDes.start_time) {
				this.search_time_dc = airTimeDes.start_time
			} else {
				this.search_time_dc = ((new Date()).toLocaleDateString()).replace(/\-/g,'/')
				this.search_time_dc = this.setTimeMonth(this.search_time_dc)
			}
			
			if (airTimeDes.end_time) {
				this.search_time_fc = airTimeDes.end_time
				this.ticType = 1
				this.ticTypeStr = '往返'
			} else {
				this.ticType = 0
				this.ticTypeStr = '单程'
			}
		},
		
		
		// 设置机票类型
		setTicType(val){
			let str = ''
			switch(val.detail.value){
				case 0:str = '单程';break;
				case 1:str = '往返';break;
				case 2:str = '联程';break;
			}
			this.ticTypeStr = str
			this.ticType = val.detail.value
		},
		
		
		
		
		// 设置是否为儿童票
		// 设置是否只看高铁车票
		setSeeGt(){
			this.IsChild = !this.IsChild
		},
		
		
		// 选择弹出层中的舱位规格并关闭弹出层
		choseSitType(item){
			this.sit_type = item
			this.showSitType(item)
			this.$refs.popup.close()
		},
		// 打开弹出层
	   open(){
		  this.$refs.popup.open()
	   },
		// 舱位名称设置
		showSitType(item){
			if (item == 0) {
				return '全部舱位'
			} else if(item == 1){
				return '经济舱'
			} else {
				return '公务舱/头等舱'
			}
		},
		
		// 改变始发地 做交换
		changeTwoCity(){
			let sbox = {...this.startCityInfo} //{...obj} 浅拷贝
			let ebox = {...this.endCityInfo}
			
			// console.log(box)
			// return
			this.startCityInfo.AirportShortName= ebox.AirportShortName
			this.endCityInfo.AirportShortName= sbox.AirportShortName
		},

		// 获取选中日期和当前日期差距日 最多后天
		setTimetoNow(type){

			let str = type
			let obj = this.$store.state.airFlightType
			if (type == 'fc') {
				uni.navigateTo({
					url: '../../pages/chosedate/chosedate?type=fifc'
				})
				return
				obj.type = 1
				this.$store.commit('setFlyType',obj) // 添加返程
			} 
			if(type == 'dc'){
				obj.type = 0
				this.$store.commit('setFlyType',obj) // 直接单程 setFlyTic
			}
			let url = "../../pages/chosedate/chosedate?type=fjpDate"
			uni.navigateTo({
				url:url
			})
		},
		// 跳转到城市选择页面选择城市
		goSeachCity(type){
			uni.navigateTo({
				url:"../../pages/choseCity/choseCity?type="+type+"&fly=fly"
			})
		},
		// 点击查询跳转到指定查询页面 并存储查询记录
		searchFJP(){
			
			
			let sName = this.startCityInfo.AirportShortName
			let eName = this.endCityInfo.AirportShortName
			let sDate = this.cfDateStr
			if (sName == '出发机场' || eName == '到达机场') {
				uni.showModal({
					title: '提示',
					content: '请完善机场信息/出发日期',
					showCancel: false,
				});
				return
			}
			let DepartCityCode = this.startCityInfo.AirportCode // 	出发城市三字码
			let ArriveCityCode = this.endCityInfo.CityCode  // 抵达城市三字码
			let IsChild =  this.IsChild// 是否是查询儿童票 
			let DepartDate = this.cfDate  // 出发日期
			let datas = {
				startCityInfo: this.startCityInfo,
				endCityInfo: this.endCityInfo,
				IsChild: this.IsChild,
				DepartDate: this.cfDate,
			}
			this.$store.commit('setAirTicSeaMes', datas)
			// 跳转
			if(this.isReading){
				this.isReading = false
				uni.navigateTo({
					url: '../../fly/hangbanlist/flyList?DepartCityCode=' + DepartCityCode + '&ArriveCityCode=' + ArriveCityCode + '&IsChild=' + IsChild + '&DepartDate=' + DepartDate
				})
			}

		},
		// 清除搜索历史记录
		clearHist(){
			localStorage.clear('qcpHist')
			this.qcpHist = []
			this.clearBtnShow = false
		},
		// 返回上级目录
		goBack() {
			 uni.navigateBack({
			 	
			 })
		}
	}
}
